import { React } from "react";

//import List from "./List"; <---- local
//import { v4 as uuidv4 } from "uuid"; <---- local
import AddItem from "./AddItem";
import TotalItems from "./TotalItems";

const AddItemsContainer = ({ shops, setShops }) => {
  // const [message, setMessage] = useState({});

  // const handleSubmit = (e) => {
  //   e.preventDefault();
  //   if (!item) {
  //     return;
  //   } else {
  //     setList([...list, { item, id: uuidv4() }]); //npm package that generates random ID's
  //     setItem("");
  //   }
  // };

  return (
    <div>
      <TotalItems shops={shops} setShops={setShops} />
      {shops.map((shop, index) => {
        if (shop.selected === true) {
          return (
            <AddItem
              key={shop.id}
              shop={shop}
              shops={shops}
              setShops={setShops}
              index={index}
            />
          );
        }
        return null;
      })}
    </div>
  );
};

export default AddItemsContainer;
