import { React, useState } from "react";

const TotalItems = ({ shops, setShops }) => {
  const [totalItemCount, setTotalItemCount] = useState(0);
  console.log(shops[0].list.length + shops[1].list.length);

  return (
    <div>
      {totalItemCount > 0 && (
        <div className="total-items">
          {totalItemCount > 1
            ? `${totalItemCount} items left`
            : `${totalItemCount} item left`}
        </div>
      )}
    </div>
  );
};

export default TotalItems;
