import { React, useState } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { FaTrash, FaGripLines, FaEllipsisH, FaCheck } from "react-icons/fa";
import { v4 as uuidv4 } from "uuid";

const AddItem = ({ shop, shops, setShops, index }) => {
  const [item, setItem] = useState("");
  const [list, setList] = useState([]);
  const [removeState, setRemoveState] = useState(false);

  const handleSubmit = (e, id, index) => {
    e.preventDefault();
    if (!item) {
      return;
    } else {
      setShops((shops) => {
        return shops.reduce((acc, shop) => {
          if (shop.id === id) {
            //     setList([...list, { item, id: uuidv4() }]); //npm package that generates random ID's
            return [...acc, { ...shop, list: [...shop.list, item] }];
          }
          return [...acc, shop];
        }, []);
      });
      setItem("");
      setShops((shops) => {
        return shops.reduce((acc, shop) => {
          if (shop.id === id) {
            return [...acc, { ...shop, listLength: shop.list.length }];
          }
          return [...acc, shop];
        }, []);
      });
    }
  };

  const handleOnDragEnd = (result) => {
    if (!result.destination) return; //handles moving the list item outside of list e.g. to right or left of screen
    //console.log(result); //gets the destination and source of the item position
    const items = Array.from(shop.list);

    const [reorderedItem] = items.splice(result.source.index, 1); //remove moved item from array
    console.log(reorderedItem);
    items.splice(result.destination.index, 0, reorderedItem); //put removed item back in destintation position
    // // setList(items); //update state after change
    console.log(items);
    setShops((shops) => {
      return shops.reduce((acc, shop) => {
        if (shop.name === result.source.droppableId) {
          return [...acc, { ...shop, list: [...items] }];
        }
        return [...acc, shop];
      }, []);
    });
  };

  const clearItem = (id, itemName) => {
    console.log(id, itemName);
    setShops((shops) => {
      return shops.reduce((acc, shop) => {
        if (shop.id === id) {
          const itemIndex = shop.list.indexOf(itemName);
          const newList = shop.list.filter((item, index) => item !== itemName);

          return [...acc, { ...shop, list: [...newList] }];
        }
        return [...acc, shop];
      }, []);
    });
  };

  const removeShop = (index) => {
    const newShops = shops;
    newShops[index].selected = !newShops[index].selected;
    setShops([...newShops]);
  };

  const removeAllShopItems = (id) => {
    setShops((shops) => {
      return shops.reduce((acc, shop) => {
        if (shop.id === id) {
          return [...acc, { ...shop, list: [] }];
        }
        return [...acc, shop];
      }, []);
    });
  };

  return (
    <div key={shop.id} className="shop-card">
      <div className="shop-card-header-container">
        <img src={shop.image} alt={shop.name} className="selected-shop-image" />
        <FaEllipsisH
          className="react-icons"
          onClick={() => setRemoveState(!removeState)}
        />
      </div>
      {shop.list.length > 0 && (
        <div className="shop-items-remaining">
          {shop.list.length > 1
            ? `${shop.name} (${shop.list.length} items left)`
            : `${shop.name} (${shop.list.length} item left)`}
        </div>
      )}
      <div className="input-container">
        <input
          className="list-input"
          type="text"
          placeholder="Add to list"
          onChange={(e) => setItem(e.target.value)}
          value={item}
        />
        <button
          className="add-btn"
          onClick={(e) => handleSubmit(e, shop.id, index)}
        >
          Add
        </button>
      </div>
      {removeState && (
        <div className="remove-container">
          <div
            className="remove-shop-container"
            onClick={() => removeShop(index)}
          >
            <p className="remove-shop-wording"> Remove Shop </p>
            <FaTrash className=" react-icons" />
          </div>
          <div
            className="remove-all-items-container"
            onClick={() => removeAllShopItems(shop.id)}
          >
            <p className="remove-all-items-wording"> Remove Items </p>
            <FaTrash className=" react-icons" />
          </div>
        </div>
      )}
      <DragDropContext onDragEnd={handleOnDragEnd}>
        <Droppable droppableId={shop.name}>
          {(provided) => (
            <div {...provided.droppableProps} ref={provided.innerRef}>
              {shops[index].list.map(
                (shopList, index) => {
                  return (
                    <Draggable
                      key={shop.list[index]}
                      index={index}
                      draggableId={shop.list[index]}
                    >
                      {(provided) => (
                        <div
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          ref={provided.innerRef}
                        >
                          <div className="list-container">
                            <FaGripLines className="react-icons" />
                            <div className="list-items">{shopList}</div>
                            <FaCheck
                              className="react-icons"
                              onClick={() =>
                                clearItem(shop.id, shop.list[index])
                              }
                            />
                          </div>
                        </div>
                      )}
                    </Draggable>
                  );
                }

                /* {list.map((listItem, index) => (
<List
key={listItem.id}
id={listItem.id}
list={listItem.item}
clearItem={clearItem}
index={index}
/>
))} */
              )}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
};

export default AddItem;
